.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  align-content: center;
  justify-content: center;
  padding-top: 5vh;
}

.app-content {
  height: 90vh;
  display: grid;
  grid-template-columns: 30vw 30vw;
  grid-gap: 30px;
}

.data-container {
  height: 90vh;
  background-color: antiquewhite;
  min-width: 300px;
  border-radius: 10px;
  padding: 0 10px;
  overflow-y: auto;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
